}

  .imageText {
    position: absolute;
    top: 0;
    background-color: rgba(75, 125, 200, 1); 
    padding: 10px 0px;
    width: 100%;
    text-align: center;
    margin: 0;
    color: white;
}

.image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.reactText {
    font-size: 15px;
}

.react .reactText {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;

    position: absolute;
    z-index: 1; 
}

.react:hover .reactText {
    visibility: visible;
}

